.partidos-cabecera {
    padding: 1.5rem 10vw;
    width: 100%;
    font-size: 0;
    &.chunta {
        .partidos-cabecera-title {
            background-color: var(--chunta);
        }
    }
    &.ciudadanos {
        .partidos-cabecera-title {
            background-color: var(--ciudadanos);
        }
    }
    &.partido-popular {
        .partidos-cabecera-title {
            background-color: var(--pp);
        }
    }
    &.zec {
        .partidos-cabecera-title {
            background-color: var(--zec);
            color: var(--white);
            border-color: var(--black);
        }
    }
}

.partidos-cabecera-izda {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 1rem;
    grid-row-gap: 1rem;
    vertical-align: top;
    border-radius: 4px;
    padding: 1rem;
    position: relative;
    background-color: #ccc;
}

.partidos-cabecera-title {
    font-size: 1.25rem;
    font-weight: normal;
    border-left: 5px solid;
    background-color: var(--psoe);
    font-family: var(--fontBislai);
    color: var(--black);
    margin: 0;
    width: intrinsic;
    /* stylelint-disable declaration-block-no-duplicate-properties */
    width: -moz-max-content;
    width: -webkit-max-content;
    /* stylelint-enable declaration-block-no-duplicate-properties */
    margin-bottom: .5rem;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: .5rem 1rem;
}

.partidos-cabecera-texto-derecha {
    font-size: 1rem;
    max-width: 80%;
    margin: 1rem auto;
}

.partidos-cabecera-dcha {
    width: 55%;
    display: inline-block;
}

.partidos-concejales {
    font-size: 1rem;
}

.partidos-concejales-listado {
    padding-left: 0;
}

.partidos-cabecera-subtitle {
  grid-column: 1 / 4;
  font-size: 1rem;
  margin: 0;
}

.partido-concejales-lista-elemento {
    font-size: 1rem;
    list-style: none;
    display: block;
    background-color: var(--white);
    border-radius: 4px;
    padding: .75rem;
    color: var(--black);
    text-decoration: none;
    transition: background-color .3s ease-in;
    &:hover {
        background-color: var(--grey);
    }
}

.concejales-icon {
    width: 16px;
    height: auto;
    display: block;
    margin-right: .5rem;
}

.partido-concejales-lista-elemento-texto {
    display: inline-block;
    vertical-align: middle;
}

.partidos-grafico {
    width: 100%;
    padding: 1rem 10vw;
    font-size: 0;
    margin: 0 auto;
    background-color: #f1f1f1;
}

.partidos-grafico-texto {
    width: 100%;
    position: relative;
}

.partidos-grafico-titulo {
    font-size: 1rem;
    font-weight: normal;
    font-family: var(--fontBislai);
    display: inline-block;
    margin: 0;
    padding: 0;
    border-radius: 4px;
}

.partidos-grafico-pp {
    .partidos-grafico-titulo {
        border-color: var(--pp);
    }
}

.partidos-grafico-zec {
    .partidos-grafico-titulo {
        border-color: var(--zec);
    }
}

.partidos-grafico-psoe {
    .partidos-grafico-titulo {
        border-color: var(--psoe);
    }
}

.partidos-grafico-cha {
    .partidos-grafico-titulo {
        border-color: var(--chunta);
    }
}

.partidos-grafico-parrafo {
    font-size: 1rem;
    font-family: var(--fontPlex);
    margin: .5rem 0 2rem;
}

.partidos-grafico-parrafo ~ .partidos-grafico-parrafo {
    margin: 0;
}

.partidos-grafico-grafica {
    max-width: 60%;
    margin: 0 auto;
    margin-top: 10vh;
}

.grafica-favor-legislatura {
    max-width: 70%;
    margin: 3rem auto;
    .partidos-grafico-parrafo {
        max-width: 25rem;
    }
    .line {
        fill: none;
    }
    text {
        font-family: var(--condensed);
    }
    .PSOE {
        fill: var(--psoe);
    }
    .CIUDADANOS {
        fill: var(--ciudadanos);
    }
    .ZEC {
        fill: var(--zec);
    }
    .PP {
        fill: var(--pp);
    }
    .CHA {
        fill: var(--chunta);
    }
    .axis-x {
        .tick line,
        .domain {
            stroke: none;
        }
    }
    .axis-y {
        .tick line {
            stroke: #ccc;
            stroke-dasharray: 0;
        }
    }
}

@media only screen and (max-width: 48em) {

    .grafica-favor-legislatura {
        max-width: 90%;
    }

    .partido-card-top {
        width: 100%;
        height: auto;
    }

    .partido-card-votos,
    .partido-card-concejales {
        width: 40%;
        display: inline-block;
        margin-right: 1rem;
        vertical-align: top;

    }

    .partidos-cabecera-izda,
    .partidos-cabecera-dcha,
    #historico {
        width: 100%;
    }

    .partidos-cabecera-dcha {
        margin-top: 1rem;
    }

    .partidos-cabecera {
        padding: 1rem;
    }

    .partidos-grafico {
        padding: 10px;
    }

    .partidos-grafico-titulo {
        font-size: 1rem;
        width: 100%;
        padding: 0;
    }

    .partidos-grafico-parrafo,
    .partidos-grafico-grafica {
        max-width: 100%;
    }

    .partidos-grafico-parrafo {
        font-size: .75rem;
    }

    .distrito {
        margin: 0 auto;
        display: inline-block;
    }

}

@media only screen and (max-width: 30em) {

    .partidos-cabecera-izda {
        display: inline-block;
    }

    .partido-concejales-lista-elemento {
        &:not(:last-child) {
            margin-bottom: .5rem;
        }
    }
}
